//  Link         .
import { Link } from "react-router-dom";
import ArrowImg from "../assets/Arrow.png";
import PagesImg from "../assets/Pages.png";
import styled from "styled-components";

const Container = styled.div`
  //  css
  background: linear-gradient(
      0deg,
      rgba(76, 157, 88, 0.21875) 60.56%,
      rgba(73, 147, 83, 0) 71.74%
    ),
    #61d271;
  height: 200vh;
  overflow: hidden;
`;
const Title = styled.h1`
  letter-spacing: -0.5vw;
  font-family: Inter;
  font-style: normal;
  font-weight: 1000;
  font-size: 12vw;
  color: #ffffff;
  margin: 10vh 6vw;
  padding: 1vw;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black,
    2px 2px 4px grey;
`;
const Arrow = styled.img`
  //   css

  width: 20vw;
  height: auto;
  margin: 3vw 6vw;
`;
const Detail = styled.p`
  //  css
  letter-spacing: -0.15vw;
  color: white;
  padding: 1vw;

  margin: 10vh 6vw;
  font-family: "Inter";
  font-style: normal;
  font-weight: 200;
  font-size: 2vw;
  line-height: 5vh;
`;
const Pages = styled.img`
  //  css

  width: 100vw;
  height: 150vh;
`;

function Main() {
  return (
    <>
      <Container>
        <Title>
            <br /> 
          <Link to={{ pathname: `/Name/*` }}>
            <Arrow src={ArrowImg} />
          </Link>
        </Title>

        <Detail>
             Knapsack     
          <br />
                  .
        </Detail>
        <Pages src={PagesImg} />
      </Container>
    </>
  );
}

export default Main;
