//     Atom  Name.js.
import styled from "styled-components";
import { Link } from "react-router-dom";
import { useRecoilState } from "recoil";
import { userNameAtom, monCookieAtom } from "../atoms";
import CookieImg from "../assets/CookieImg.png";

const Container = styled.div`
  background-color: #fafafa;
  padding: 5vw;
`;

const Title = styled.h1`
  letter-spacing: -0.5vh;
  font-family: Inter;
  font-style: normal;
  font-weight: 100;
  font-size: 8vh;
  line-height: 10vh;
  color: #464646;
  margin: 2vw 0vw;
`;
const Detail = styled.p`
  letter-spacing: -0.2vh;
  font-family: Inter;
  font-weight: 400;
  font-size: 3vh;
  line-height: 4vh;
  color: #464646;
`;
const Questions = styled.div`
  margin: 3vw 0vw;
  display: flex;
`;
const Question = styled.h2`
  letter-spacing: -0.2vh;
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 6vh;
  line-height: 7vh;
  width: 45vw;
  color: #464646;
  min-width: 200px;
`;
const Input = styled.input`
  padding: 2vh 1vw;
  font-size: 3vh;
  margin: 3vh 0px;
  font-family: "Inter";
  border: 1px solid #61d271;
  border-radius: 1vh;
`;
const Button = styled.div`
  text-align: center;
  padding: 2vh 1vw;
  font-size: 2vh;
  margin: 3vh 0px;
  font-family: "Inter";
  color: #754b36;
  font-weight: 700;
`;

function Name() {
  const [name, setNameAtom] = useRecoilState(userNameAtom);
  const [cookie, setCookieAtom] = useRecoilState(monCookieAtom);

  const onNameHandler = (event) => {
    setNameAtom(event.currentTarget.value);
  };
  const onCookieHandler = (event) => {
    setCookieAtom(event.currentTarget.value);
  };

  return (
    <Container>
      <Title>  !</Title>
      <Detail>
                  
        . <br />
              ! <br />
              :-)
      </Detail>
      <Questions>
        <Question>
          <p>
             
            <br />
            ?
          </p>
          <Input
            valutype="text"
            value={name}
            onChange={onNameHandler}
            placeholder=" "
          ></Input>
        </Question>

        <Question>
          <p>
               
            <br />
              ?
          </p>
          <Input
            valutype="text"
            value={cookie}
            onChange={onCookieHandler}
            placeholder="   "
          ></Input>
        </Question>
      </Questions>
      <Button>
        <Link to={{ pathname: `/Choice/*` }}>
          <img src={CookieImg} style={{ width: "10vh", margin: "1vh" }} />
        </Link>
        <p> !</p>
      </Button>
    </Container>
  );
}

export default Name;
