/*
[ ]
1. checkedItems submit   SelectedAtom . []
2.      .    
3. styled component 
*/
import { Link } from "react-router-dom";
import Webtoon from "../components/Webtoon";
import { useRecoilState } from "recoil";
import { userNameAtom, webtoonsAtom, selectedAtom } from "../atoms";
import { useState } from "react";
import styled from "styled-components";
import CookieImg from "../assets/CookieImg.png";

const Container = styled.div`
  background-color: #fafafa;
  padding: 5vw 12vw;
`;
const Title = styled.h1`
  letter-spacing: -0.5vh;
  font-family: Inter;
  font-style: normal;
  font-weight: 100;
  font-size: 8vh;
  line-height: 10vh;
  color: #464646;
  margin: 2vw 0vw;
`;
const Detail = styled.p`
  letter-spacing: -0.2vh;
  font-family: Inter;
  font-weight: 400;
  font-size: 3vh;
  line-height: 4vh;
  color: #464646;

  margin-bottom: 10vh;
`;
const Button = styled.div`
  text-align: center;
  padding: 2vh 1vw;
  font-size: 2vh;
  margin: 3vh 0px;
  font-family: "Inter";
  color: #754b36;
  font-weight: 700;
`;
const Week = styled.span`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;
const Day = styled.div`
  border: 1px solid grey;
  padding: 1vw;
  h3 {
    text-align: center;
    font-family: "Inter";
    font-style: normal;
    font-weight: 500;
    font-size: 3vh;
    margin-bottom: 2vh;
    color: #464646;
  }
`;
function Choice() {
  const [webtoons, setWebtoons] = useRecoilState(webtoonsAtom);
  const [selected, setSelected] = useRecoilState(selectedAtom);

  const userName = useRecoilState(userNameAtom);
  const [checkedItems, setCheckedItems] = useState(new Array());
  console.log(webtoons);
  const checkedItemHandler = (key, isChecked) => {
    if (isChecked) {
      checkedItems.push(key);
      setCheckedItems(checkedItems);
    } else if (!isChecked) {
      for (var i = 0; i < checkedItems.length; i++) {
        if (checkedItems[i] === key) {
          checkedItems.splice(i, 1);
          i--;
        }
      }
      setCheckedItems(checkedItems);
    }
    console.log(checkedItems);
  };
  const selectedHandler = (key, isChecked) => {
    setSelected(checkedItems);
    console.log(selected);
  };

  return (
    <Container>
      <Title>  .</Title>
      {/*   Webtoon  */}
      <Detail>
             .
        <br />
           {userName}     
        . <br />
         !        .
      </Detail>

      <Button onClick={selectedHandler}>
        <Link to={{ pathname: `/Like/*` }}>
          <img src={CookieImg} style={{ width: "10vh", margin: "1vh" }} />
        </Link>
        <p>
           , <br />
           !
        </p>
      </Button>
      <Week>
        <Day>
          <h3></h3>
          {webtoons.Mon.map((webtoon, index) => (
            <>
              <Webtoon
                key={webtoon.id}
                webtoon={webtoon}
                checkedItemHandler={checkedItemHandler}
              ></Webtoon>
            </>
          ))}
        </Day>
        <Day>
          <h3></h3>
          {webtoons.Tue.map((webtoon, index) => (
            <>
              <Webtoon
                key={webtoon.id}
                webtoon={webtoon}
                checkedItemHandler={checkedItemHandler}
              ></Webtoon>
            </>
          ))}
        </Day>
        <Day>
          <h3></h3>
          {webtoons.Wed.map((webtoon, index) => (
            <>
              <Webtoon
                key={webtoon.id}
                webtoon={webtoon}
                checkedItemHandler={checkedItemHandler}
              ></Webtoon>
            </>
          ))}
        </Day>
        <Day>
          <h3></h3>
          {webtoons.Thu.map((webtoon, index) => (
            <>
              <Webtoon
                key={webtoon.id}
                webtoon={webtoon}
                checkedItemHandler={checkedItemHandler}
              ></Webtoon>
            </>
          ))}
        </Day>
        <Day>
          <h3></h3>
          {webtoons.Fri.map((webtoon, index) => (
            <>
              <Webtoon
                key={webtoon.id}
                webtoon={webtoon}
                checkedItemHandler={checkedItemHandler}
              ></Webtoon>
            </>
          ))}
        </Day>
        <Day>
          <h3></h3>
          {webtoons.Sat.map((webtoon, index) => (
            <>
              <Webtoon
                key={webtoon.id}
                webtoon={webtoon}
                checkedItemHandler={checkedItemHandler}
              ></Webtoon>
            </>
          ))}
        </Day>
        <Day>
          <h3></h3>
          {webtoons.Sun.map((webtoon, index) => (
            <>
              <Webtoon
                key={webtoon.id}
                webtoon={webtoon}
                checkedItemHandler={checkedItemHandler}
              ></Webtoon>
            </>
          ))}
        </Day>
      </Week>
    </Container>
  );
}

export default Choice;
